*, *:before, *:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

li {
    list-style: none;
}
a {
    text-decoration: none;
}

button {
    outline: none;
    background-color: transparent;
    border: none;
}

body {
    font-family: 'Noto Sans', sans-serif;
}

input {
    border: none;
}